import React,{ Component }  from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import { Card ,Button,Input,Popover,Divider} from 'antd';
import { QuestionCircleOutlined} from '@ant-design/icons';
import TokenFactory from '../../../ABI/TokenFactory';
import {CopyToClipboard} from 'react-copy-to-clipboard';

class Hangqinadd extends Component {
      state = {
        name:'',
        symbol:'',
        decimals:'18',
        _totalSupply:'',
        tokenOwnerAddress:'',
        user:'',
        fee:'',
        message:'',
        newtokenAdr:'',
        GXvalue:'',
        chainIdDisabled:'',
      };

  async componentDidMount(){

    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
        //初始化Provider
        const provider = window['ethereum']

        //获取网络ID
        if(provider.chainId != 0x80){
          this.setState({chainIdDisabled:"disabled"})
          this.setState({message:"您当前帐户非火币链，请切换至火币链"})
        };

        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()
      }catch(e){}
    }else{console.log('没有metamask')}

    const _fee = await TokenFactory.methods.feeHT().call();
    const fee = parseFloat(web3.utils.fromWei(_fee,"ether")).toFixed(3)
    this.setState({fee});

    const GXvalue = await TokenFactory.methods.GXvalue().call();
    this.setState({GXvalue});
 

    this.setState({tokenOwnerAddress:window.defaultAccount});
    this.setState({user:window.defaultAccount});
  }

  getLastERC20 = async () => {
    const newtokenAdr = await TokenFactory.methods.getLastERC20().call({from:window.defaultAccount});
    if(newtokenAdr == 0x0){
      this.setState({newtokenAdr:'您尚未发行过代币'});
    }else{
    this.setState({newtokenAdr});
    }
  }

  createERC20 = async () =>{
    const _fee = await TokenFactory.methods.feeHT().call();
    if(this.state._totalSupply <= 1000000000000000){
    this.setState({message:"等待打包……"});
    await TokenFactory.methods.createERC20(
        this.state.name,
        this.state.symbol,
        this.state.decimals,
        web3.utils.toWei(this.state._totalSupply,'ether'),
        this.state.tokenOwnerAddress,
        )
    .send({from:window.defaultAccount,value:_fee});
    const newtokenAdr = await TokenFactory.methods.getLastERC20().call({from:window.defaultAccount});
    this.setState({newtokenAdr});
    this.setState({message:"创建成功"});
    }else{this.setState({message:"总量不得大于1000万亿"});}
  }

  render(){

    const content = (
      <div>
        <p>您的当前帐号，方便您核对</p>
      </div>
    );
      const content1 = (
        <div>
          <p>填写代币的全称，比如：bitcoin</p>
          <p>注意这个名称不是钱包或者交易所显示的名称。</p>
        </div>
      );
      const content2 = (
        <div>
          <p>填写代币的简称，比如：BTC</p>
          <p>这个名称才是钱包或者交易所显示的名称</p>
        </div>
      );
      const content3 = (
        <div>
          <p>代币的小数位，通常为18</p>
          <p>建议不要更改</p>
        </div>
      );
      const content4 = (
        <div>
          <p>代币总发行量，最多：1000000000000000</p>
        </div>
      );
      const content5 = (
        <div>
          <p>代币初始持有人</p>
          <p>默认为当前帐号，也可以指定其它帐号</p>
        </div>
      );
      const content6 = (
        <div>
          <p>首页--挖矿--运回</p>
        </div>
      );

  return (
    <div>
        <Card type="inner" size='small' title="火币链一键发币------总量固定ERC20代币" >
              <div >
                
                <Input  id='i1' addonBefore="帐号" value={this.state.user} 
                    suffix={
                      <Popover  content={content} trigger="click">
                      <QuestionCircleOutlined id='i1' /> 
                      </Popover>
                    }
                    disabled/>
              </div>

          <Divider>发行代币</Divider>

                <div style={{marginTop:'10px'}}>
                      <Input  addonBefore='代币名称'  value={this.state.name} 
                          placeholder="如：bitcoin" size="large"
                          suffix={
                            <Popover content={content1} trigger="click">
                              <QuestionCircleOutlined />  
                            </Popover>
                          }
                          onChange={event=>{this.setState({name:event.target.value})}}/>
                </div>

                <div style={{marginTop:'10px'}}>
                      <Input  addonBefore='代币符号' value={this.state.symbol}
                          placeholder="如：BTC" size="large"
                          suffix={
                            <Popover content={content2} trigger="click">
                              <QuestionCircleOutlined />  
                            </Popover>
                          }
                          onChange={event=>{this.setState({symbol:event.target.value})}}/>
                </div>

                <div style={{marginTop:'10px'}}>
                      <Input  addonBefore='发行总量' value={this.state._totalSupply}
                            placeholder="如：21000000" size="large"
                            suffix={
                              <Popover content={content4} trigger="click">
                                <QuestionCircleOutlined />  
                              </Popover>
                            }
                            onChange={event=>{this.setState({_totalSupply:event.target.value})}}/>
                </div>

                <div style={{marginTop:'10px'}}>
                      <Input style={{width:'50%'}} addonBefore='小数位' value={this.state.decimals} 
                            placeholder="通常用 18" size="small"
                            suffix={
                              <Popover content={content3} trigger="click">
                                <QuestionCircleOutlined />  
                              </Popover>
                            }
                            onChange={event=>{this.setState({decimals:event.target.value})}}/>
                </div>

                <div style={{marginTop:'10px'}}>代币接受地址：
                      <Popover content={content5} trigger="click">
                            <QuestionCircleOutlined /> 
                      </Popover>
                </div>
                <Input  value={this.state.tokenOwnerAddress} placeholder="代币接受地址" 
                      onChange={event=>{this.setState({tokenOwnerAddress:event.target.value})}}/>

                <Button  shape='round' block type="primary"  size="middle" style={{marginTop:'10px'}} 
                      disabled = {this.state.chainIdDisabled}
                      onClick={() => {this.createERC20()}}>发行代币</Button>

                <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                        {this.state.message}
                </div>
                <Divider>发后查询</Divider>
 
                <div >
                    <div>
                        <Button  shape='round'   block size="middle"  
                            onClick={() => {this.getLastERC20()}}>查询新代币合约地址</Button>
                    </div>

                    <div style={{marginTop:'5px'}}>新代币合约地址：
                        <Input id='i1' value={this.state.newtokenAdr} disabled placeholder="新代币合约地址："
                            suffix={
                                <CopyToClipboard  text={this.state.newtokenAdr}>
                                <Button  size="small" >复制</Button>
                                </CopyToClipboard>
                            }
                         />
                    </div>
                </div>
         </Card>
         <div style={{marginLeft:'10px'}}>注：
            <div>发行费用：{this.state.fee}HT(实时浮动)</div>
            {/* <div>额外获得：{web3.utils.fromWei(this.state.GXvalue,'ether')}LG &nbsp;
                        <Popover content={content6} trigger="click">
                              <QuestionCircleOutlined /> 
                        </Popover></div> */}
            <div>请务必将每一项<QuestionCircleOutlined />都点开查看清楚<br/>
                一经发送无法更改与退款<br/>
                如果发生多付款等问题请Email
                </div>
            <div>Email：lgqklxyz@gmail.com 
                <CopyToClipboard text='lgqklxyz@gmail.com'>
                      <Button size='small' >复制</Button>
                </CopyToClipboard>
            </div>
            <div>
                免责声明：<br/>
                请合法合规使用您发行的代币<br/>
                非法使用与本网站无关
            </div>
        </div>
    </div>
  );
}
}

export default Hangqinadd;
