import React,{ Component }  from 'react';
import '../../App.css';
import web3 from "../../ABI/web3";
import { Link } from 'react-router-dom';
import { Table, Card ,Button,Modal} from 'antd';
import HangQin from '../../ABI/hangqin';
import erc20abi from '../../ABI/ERC20.abi.json';
import {CopyToClipboard} from 'react-copy-to-clipboard';


class Hangqin extends Component {
      state = {
        requests:[],
        HBid:'',
        HBpassword:'0',
        onoff:'none',
        onoff2:'none',
        lastHB:'',
        lastHBsymbol:'',
        message:"",
        visible:false,
        tokenadr:'',
        tokenName:'',
      };

      

  async componentDidMount(){


    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
    
        //初始化Provider
        const provider = window['ethereum']
        
        if(provider.chainId == 0x1){this.setState({chainId:"以太链"});
          this.setState({disabled:"disabled"})};
  
        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()

        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
  
        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
  
      }catch(e){}
    }else{console.log('没有metamask')}

  const feeID = await HangQin.methods.feeID().call();

    const dataSource = [];
    for(var i=11; i<=feeID; i++ ){
      const tokenPrice = await HangQin.methods.getTOKENprice(i).call();
      
      const ERC20adr = await HangQin.methods.getTOkenInfo(tokenPrice[0]).call();
      tokenPrice[5]=ERC20adr[2];
      

      if(tokenPrice[2].length < 16){
          tokenPrice[2] = parseFloat(web3.utils.fromWei(tokenPrice[2],"ether")).toExponential(2);
        } else{
          tokenPrice[2] = parseFloat(web3.utils.fromWei(tokenPrice[2],"ether")).toFixed(3);
         }
      if(tokenPrice[2] > 1000){
        tokenPrice[2] = parseFloat (tokenPrice[2]) .toFixed(0);
      }
      if(tokenPrice[0] != 0 ){
      dataSource.push(tokenPrice);
      }
    }
      this.setState({requests:dataSource});
  }

  ShowAssets= async() =>{
    const data = this.state.requests;

    console.log(data)

    for(var i=0; i<data.length; i++ ){
      const ERC20adr = await HangQin.methods.getTOkenInfo(data[i][0]).call();
      const ERC20 = await new web3.eth.Contract(erc20abi.abi,ERC20adr[2]);
      const balance =  await ERC20.methods.balanceOf(window.defaultAccount).call(); //原始余额用来计算单价
      const __balance = parseFloat(balance*10**(- ERC20adr[5]) ) //减除了18位小数的余额

      //数量科学计数
      var balanceII = '';
      if(__balance > 100000){
        balanceII = __balance.toExponential(1)
      }else{
        balanceII = __balance.toFixed(1)
      }

      //总价值科学计数
      var balanceRMB = '';
      balanceRMB = parseFloat(__balance * data[i][2] )
      if(balanceRMB < 0.1){
        balanceRMB = balanceRMB.toExponential(1)
      }else{
        balanceRMB = balanceRMB.toFixed(1)
      }

      if(__balance != 0 ){data[i][3]= balanceII}
      if(balanceRMB != 0){data[i][4]= balanceRMB}
      }
      this.setState({requests:data});

      console.log(data)
  }

  handleOk = () => {
    this.setState({visible: false})
  };
    

  render(){

    const columns = [
      {
        title: '名称',
        dataIndex: '1',
        width: 60,
        align: 'left',
      },
      {
        title: '数量',
        dataIndex: '3',
        width: 60,
        align: 'left',
      },
      
      
      {
        title: '折合￥',
        dataIndex: '4',
        width: 75,
        align: 'left',
      },

      {
        title: '价格(￥)',
        dataIndex: '2',
        align: 'right',
      }
    ];

  return (
    <div>
    <Card  type="inner" size='small' >

      <Table pagination={false} columns={columns}  dataSource={this.state.requests} 
           onRow = {(record) => {
            return {
              onClick: () => {
                {this.setState({visible: true})};
                {this.setState({tokenadr: record[5]})}
                {this.setState({tokenName: record[1]})}
                
                }
              }
              }
              }         

      /> 
      
    </Card>
    <Card>
        <Button shape='round' type="primary" block color='green' size="middle" 
            style={{marginBottom:'10px'}}
            onClick={() => {this.ShowAssets()}}
            >显示我的资产</Button>
        <Button shape='round' block color='green' size="middle" ><Link to="/Hangqinadd">添加通证(全网可见)</Link></Button>
        <div >注：
          <div>2.8e-7是什么意思？</div>
          <div>科学计数法，2.8e-7=0.00000028</div>
        </div>
        
    </Card>
    <Modal title={this.state.tokenName} visible={this.state.visible} onOk={this.handleOk} onCancel={this.handleOk} >
        <p>通证地址:</p>
        <p>{this.state.tokenadr}</p>
        <p>
          <CopyToClipboard text={this.state.tokenadr}>
            <Button >复制</Button>
          </CopyToClipboard>
        </p> 
      </Modal>
      
    </div>
  );
}
}

export default Hangqin;
