import React,{ Component }  from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import { Link } from 'react-router-dom';
import { Table,Switch , Card ,Button,Input,Popover, Result} from 'antd';
import { CloseOutlined,CheckOutlined , QuestionCircleOutlined} from '@ant-design/icons';
import Hongbao from '../../../ABI/hongbao';
import Message from '../../../ABI/message';
import erc20abi from '../../../ABI/ERC20.abi.json';
const { TextArea } = Input;



class HBhome extends Component {
      state = {

        issave:'0',
        addressTo:'',
        _message:'',



        requests:[],
        HBid:'',
        HBpassword:'0',
        onoff:'none',
        onoff2:'none',
        lastHB:'',
        lastHBsymbol:'',
        message:"",
        ye:'',
        
      };

  async componentDidMount(){


    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()

         //初始化Provider
         const provider = window['ethereum']

        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()

      }catch(e){}
    }else{console.log('没有metamask')}

  }


  send =async () => {
    this.setState({message:"等待打包……"});
    await Message.methods.sendMsg(this.state.addressTo, this.state._message,this.state.issave)
    .send({from:window.defaultAccount});
    this.setState({message:"领取成功请点击'查询结果'"});
  }

 
  render(){

      const content = (
        <div>
          <p>1. 手续费及挖矿比率可能会适时调整</p>
          <p>2. 发红包者可以在首页贡献值内领取LG矿，不用每发一次红包就领一次，可以一个月领一次</p>
          <p>3. 手续费收益的10~50%将用于回购LG或添加LG流动池</p>
        </div>
      );


  return (
    <div>
          <div>
              <Button    color='green' size="middle"  >
                <Link to="/messageHome">收件箱</Link>
              </Button>
              &nbsp;
              <Button   color='green' size="middle">
                <Link to="/HBgive">朋友圈</Link>
              </Button>
          </div>
      <Card type="inner" size='small'>
      <div>
          <div>发件人：
              <Input style={{ width: '75%' }}   placeholder="粘贴地址：如'0x0123...'" 
                          onChange={event=>{this.setState({addressTo:event.target.value})}}/>
          </div>
          <div>内&nbsp;&nbsp;&nbsp;&nbsp;容(切勿输入助记词密码及您的隐私)：
              <TextArea rows={10} placeholder="输入内容" 
                onChange={event=>{this.setState({_message:event.target.value})}}
              />
          </div>
          <Button  shape='round' type="primary" block color='green' size="middle" style={{marginTop:'5px'}} 
            onClick={() => {this.send()}}
          >发送
          </Button>
          <div style={{marginTop:'10px'}}>
            <Switch
              checkedChildren={<CheckOutlined />}
              unCheckedChildren={<CloseOutlined />}
              
              onChange={()=>this.setState({issave:'1'})}
              
            />
            &nbsp;&nbsp;保存至储存箱
          </div>
      </div>
      </Card>
    

    
    <Card type="inner" size='small'  >
    <Button shape='round' type="dashed" block  size="small"  onClick={() => {this.open2()}}>说明</Button>
      <div style={{display:this.state.onoff2}}>
          <div>1. 持用100或以上LG，是发、领红包的前提条件</div>
          <div>2. 一个帐号只可领取一次</div>
          <div>3. 红包总额5%挖矿LG由发红包者获得，5%为手续费用于回馈所有LG成员
                <Popover content={content} title="说明" trigger="click">
                    <QuestionCircleOutlined />
                </Popover>
          </div>
          <Button shape='round' block type="dashed" size="small" onClick={() => {this.close2()}}>关闭</Button>
      </div>
    </Card>
    </div>
  );
}
}

export default HBhome;
